@font-face {
  font-family: Digital-7;
  src: url("Digital-7.3c0c9cb8.woff2") format("woff2"), url("Digital-7.95c5d624.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

.App {
  min-height: 100vh;
}

.App__dropzone {
  cursor: pointer;
  background-color: #0000;
  border: 2px dashed #ccc;
  border-radius: 10px;
  width: 100%;
  min-height: 300px;
  padding: 20px;
  transition: all .2s ease-in-out;
}

.App__dropzone--dragging {
  border-color: var(--bs-blue);
  background-color: #ffffff1a;
}

img {
  max-width: 100%;
  height: auto;
}

.font-sans-serif {
  font-family: "sans-serif";
}

.font-serif {
  font-family: "serif";
}

.font-monospace {
  font-family: "monospace";
}

.font-cursive {
  font-family: "cursive";
}

.font-fantasy {
  font-family: "fantasy";
}

.font-Digital-7 {
  font-family: Digital-7;
}

.font-Orbitron {
  font-family: Orbitron;
}

.font-Quantico {
  font-family: Quantico;
}

.btn svg {
  fill: currentColor;
  width: 1.25em;
  height: 1.25em;
}

.btn:has(svg) {
  grid-template-columns: auto 1.25em;
  align-items: center;
  column-gap: .25em;
  display: grid;
}
